import {Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {IntegrationService} from './../../../shared/services/integration.service';
import {SubDomainService} from './../../../shared/services/subdomain.service';
import {CookieService} from './../../../shared/services/cookie.service';
import { Integrations } from './../../../shared/models/integrations';
declare var window: any;
declare var jQuery: any;

@Component({
    selector: 'app-og-integrations',
    template: require('./integrations.component.html'),
    styles: [require('./integrations.component.css'), require('./../../../../assets/css/sahil-hover.css'), require('./../../../../assets/css/custom-material.css'),require('./../settings.component.css')]
})

export class IntegrationsComponent implements OnInit {
   company: String = '';
   addKey: FormGroup;
   addMarketo: FormGroup;
   testMarketoForm: FormGroup;
   error: boolean = false;
   errorMessage: string;
   isSyncEnableMarketo: boolean = false;
   isSyncEnableSF: boolean = false;
   marketType: string = '';
   pendingLeadsMarketo: string;
   pendingLeadsSF: string;
   isHubspotConnected: boolean = false;
   isMarketoConnected: boolean = false;
   salesforceError: string;
   isSalesforceError: boolean = false;
   integrationType: string;
   integrations: any = new Integrations({});
   configuration: any = {
        salesforce: false,
        marketo: false
   };
   constructor(private _integrationService: IntegrationService, private fb: FormBuilder,
               private _cookieService: CookieService,  private _subdomainService: SubDomainService) {
        this.company = localStorage.getItem('company');
   }

   ngOnInit() {
       this.getConfiguration();
       this.isSyncEnableMarketo = false ;
       this.isSyncEnableSF = false;
       this.getAllConfiguration();
       this.getsyncLead();
       this.addKey = this.fb.group({
           hapikey : ['', Validators.compose([Validators.required])]
       });

       this.addMarketo = this.fb.group({
           client_id : [this.integrations.client_id, Validators.compose([Validators.required])],
           client_secret : [this.integrations.client_secret, Validators.compose([Validators.required])],
           ideantity : [this.integrations.ideantity, Validators.compose([Validators.required])]
       });

      this.testMarketoForm = this.fb.group({
        firstName : [''],
        email : ['', Validators.compose([Validators.required])],
        lastName : ['']
      });

       jQuery.material.init();
   }

   connectSalesforce() {
      jQuery('#mapping-int-toAccess').modal('hide');
       let data = {} ;
        this._integrationService.getLink(data, 'salesforce')
        .subscribe((response) => {
             let companyId: string = <string>this._subdomainService.subDomain.company_id;
             this._cookieService.createCookie('comp', companyId , 3);
             let newWindow = window.open(response, 'GoogleWindow', 'width=600, height=500,scrollbars=yes');
             let self = this;
             let interval = setInterval(function(){
                 let cookie = self._cookieService.readCookie('comp');
                 if (cookie === 'success') {
                     newWindow.close();
                     self.getAllConfiguration();
                     window.toastNotification('You have Successfully added salesforce for integration.');
                     clearInterval(interval);
                 }else if (cookie === null) {
                       clearInterval(interval);
                 }
             }, 5000);
        }, (error) => {
            console.log(error);
        });
   }

    getAllConfiguration() {
    this._integrationService.getAllConfiguration()
      .subscribe((result) => {
        this.configuration.salesforce = result.salesforce ? result.salesforce : false;
        this.configuration.marketo = result.marketo ? result.marketo : false;
        console.log(this.configuration, '$$$$$$$$$$$$$$$$$$$');
      }, (error) => {
        console.log(error);
      });
  }

   getConfiguration() {
    this._integrationService.getConfiguration()
      .subscribe((result) => {
         if (result.marketo_config) {
            this.integrations.client_id = result.marketo_config.client_id;
            this.integrations.client_secret = result.marketo_config.client_secret;
            this.integrations.ideantity = result.marketo_config.instance_url;
         }
      }, (error) => {
        console.log(error);
      });
   }

  getsyncLead() {
      this._integrationService.getCompanySyncLeads()
      .subscribe((result) => {
       this.pendingLeadsSF = result.salesforce + ' item';
       this.pendingLeadsMarketo = result.marketo + ' item';
       if (this.pendingLeadsMarketo >= '1') {
           this.isSyncEnableMarketo = true;
       } if (this.pendingLeadsSF >= '1') {
           this.isSyncEnableSF = true ;
       }
      }, (error) => {
        console.log(error);
      });
  }


  syncLead(type: string) {
      jQuery('#sync-leads').html('Please Wait..');
      this._integrationService.syncLeads(type)
      .subscribe((result) => {
          jQuery('#sync-leads').html('Sync');
          if (type === 'marketo') {
                 this.isSyncEnableMarketo = false;
          }else {
              this.isSyncEnableSF = false ;
          }
          this.getsyncLead();
           window.toastNotification('Leads will synced to connected ' + type + ' account for this company');
      }, (error) => {
          this.isSyncEnableMarketo = true;
          this.isSyncEnableSF = true ;
          if (type === 'marketo') {
            jQuery('#sync-leads').html('Sync (' + this.pendingLeadsMarketo + ')');
          }else {
            jQuery('#sync-leads').html('Sync (' + this.pendingLeadsSF + ')');
          }
          window.toastNotification('Some error occured!! Please Reconfigure ' + type);
      });
  }

//    connectHubspot() {
//        jQuery('#btnSave').html('please Wait...').attr('disabled', true);
//        let data = {
//            hapikey : this.addKey.value.hapikey,
//            company : this.company


//        };
//        this._integrationService.connectHubspot(data)
//         .subscribe((response) => {
//             console.log(response);
//             jQuery('#add-new-hapikey').modal('hide');
//             window.toastNotification('You have Successfully added Hubspot for integration ');
//             this.gethubSpot();
//         }, (error) => {
//             this.error = true;
//             this.errorMessage = error.error.err_message;
//             console.log(error);
//              jQuery('#btnSave').html('Save Hapikey').attr('disabled', false);
//         });
//    }


   openModal() {
       jQuery('#add-new-hapikey').modal('show');
   }
   openMarketo() {
        jQuery('#add-marketo').modal('show');
   }
   openSalesforce() {
     jQuery('#mapping-int-toAccess').modal('show');
     this.integrationType = 'salesforce';
   }
   connectMarketo() {
       jQuery('#btnmMarketo').html('please Wait...').attr('disabled', true);
        let data = {
            client_id : this.addMarketo.value.client_id,
            client_secret : this.addMarketo.value.client_secret,
            restEndpoint : this.addMarketo.value.ideantity,
            company : this.company
        };
       this._integrationService.authorization(data, 'marketo')
        .subscribe((response) => {
            if (response.errors) {
                this.errorMessage = 'Incorrect Endpoints. Please re-enter correct endpoint for Marketo.';
                this.error = true;
                jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
             }else if (response.error) {
               if (response.error === 'unauthorized')  {
                   this.errorMessage = 'Invalid Credentials - Please re-enter your Marketo Client Id';
               }else if (response.error === 'invalid_client') {
                    this.errorMessage = 'Invalid Credentials - Please re-enter your Marketo Client Secret';
               }else {
                    this.errorMessage = response.error_description;
               }
               this.error = true;
               jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
             }else {
                this.error = false;
                jQuery('#btnmMarketo').html('Reconfigure Marketo').attr('disabled', false);
                this.configuration.marketo = true;
                jQuery('#add-marketo').modal('hide');
                window.toastNotification('You have Successfully added Marketo for integration, Please test it by clicking on Test Connection.');
            }
        }, (error) => {
          if (error.error.code === 'ENOTFOUND' || error.error.code === 'E_UNEXPECTED') {
                 this.errorMessage = 'Authentication error - Please re-enter the end points';
            }else {
                this.errorMessage = error.error.message;
            }
          this.error = true;
          jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
        });
   }
   change() {
       this.error = false;
   }


   // gethubSpot() {
   //      this._integrationService.geHubspotData(this.company)
   //      .subscribe((response) => {
   //         this.isHubspotConnected = true;
   //      }, (error) => {
   //          console.log(error);
   //      });
   // }


}





